<template>
  <div id="login">
    <div class="login2">
      <div style="margin-left: 450px;margin-top: 30px">
        <p style="color:#9b9b9b">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;QQ群：218851843</p>
        <img :src="imgUrl" width="200px">
        <p style="color:#9b9b9b">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有疑问加群咨询</p>
      </div>
      <div style="margin-top: -300px">
        <h2>用户登录</h2>
        <el-form ref="form" :model="form" label-width="">
          <el-form-item>
            <el-input v-model="form.id" style="width: 300px;" placeholder="学号" prefix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="form.school" placeholder="请选择学校" style="width: 300px">
              <el-option v-for="(x,index) in schools" :key="index" :value="x.schoolId"
                         :label="x.schoolName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-input v-model="form.password" style="width: 300px" placeholder="密码" show-password
                      prefix-icon="el-icon-key"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit(form.id,form.school,form.password)" style="width: 300px">登录
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div>
        <el-link type="primary" :underline="false" @click="toRegister()">新用户注册</el-link>
        <el-link type="primary" style="margin-left: 120px" :underline="false" @click="EmailLogin()">邮箱验证码登录</el-link>
      </div>
      <el-link type="info" style="margin-top: 50px;margin-left: 90px" :underline="false" @click="OtherLogin()">其他方式登录>
      </el-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      form: {
        id: '',
        password: '',
        school: '',
      },
      imgUrl: require("@/assets/qrcode.jpg"),
      schools: []
    }
  },
  methods: {
    onSubmit(id, school, password) {
      if (id == '') {
        this.$alert('学号未输入', '错误', {
          confirmButtonText: '确定',
        });
      } else if (school == '') {
        this.$alert('学校id未输入', '错误', {
          confirmButtonText: '确定',
        });
      } else if (password == '') {
        this.$alert('密码未输入', '错误', {
          confirmButtonText: '确定',
        });
      } else {
        axios('/open/login/number', {
          params: {
            schoolId: school,
            userNumber: id,
            userPassword: password,
          },
          method: 'post'
        }).then(res => {
          // console.log(school)
          // console.log(id)
          // console.log(password)
          console.log(res.data)
          if (res.data.code == 500105) {
            this.$alert('学号、学校或密码错误', '错误', {
              confirmButtonText: '确定',
            });
          }
          // console.log(res.data.data.school.schoolName)
          // console.log(res.data.data.userGender)
          let userInfo = {
            userId:res.data.data.userId,
            userName: res.data.data.userName,
            userGender: res.data.data.userGender,
            userTel: res.data.data.userTel,
            userPassword: res.data.data.userPassword,
            userEmail: res.data.data.userEmail,
            schoolName: res.data.data.school.schoolName,
            userHead:res.data.data.userHead,
            studentCollege:res.data.data.studentClass.studentCollege,
            studentClassGrade:res.data.data.studentClass.studentClassGrade,
            studentClassMajor:res.data.data.studentClass.studentClassMajor,
            studentClassNumber:res.data.data.studentClass.studentClassNumber,
            studentClassIndex:res.data.data.studentClass.studentClassIndex,
          }
          this.$store.commit('updateUser', userInfo)
          this.$store.commit('updateuserId',userInfo.userId)
          this.$router.push('/Home/Course')
        }).catch(function (error) {
          console.log(error);
        });
      }
    },
    toRegister() {
      this.$router.push('/Register');
    },
    EmailLogin() {
      this.$router.push('/EmailLogin')
    },
    OtherLogin() {
      this.$router.push('/OtherLogin')
    },
  },
  mounted() {
    axios('/open/school', {
      method: "get",
    }).then(res => {
      // console.log(res.data)
      this.schools = res.data.data
      // console.log(this.schools)
    }).catch(function (error) {
      console.log(error);
    });
  }
}
</script>

<style scoped>

#login {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #e6e6e6;
}

.login2 {
  border-radius: 20px;
  margin-left: 23%;
  margin-top: 6%;
  padding: 80px;
  background: white;
  box-shadow: 4px 4px 4px #ccc;
  width: 700px;
  height: 360px;
}

</style>



